.modal-backdrop {
  width: 100%;
  height: 100%;
  z-index: 12;
  background: rgba(0, 0, 0, .2);
  transition-property: opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
  transform: scale(100%);
}

.modal-backdrop.modal-is-hidden.modal-window {
  transition-property: opacity, visibility, transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  transform: translate(-50%, -50%)scale(0)skewY(-90deg);
}

